@import "../../../styles/vars";
@import "../../../styles/mixins";

.serverErrorBlock {
  padding-top: 300px;
  padding-bottom: 130px;

  @include xlScreenMedia {
    padding-top: 200px;
    padding-bottom: 120px;
  }

  @include lgScreenMedia {
    padding-top: 100px;
    padding-bottom: 80px;
  }

  @include smScreenMedia {
    padding-top: 72px;
    padding-bottom: 16px;
  }

  &__wrapper {
    max-width: 1430px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    width: calc(100% - 35px);

    @include smScreenMedia {
      width: 100%;
    }
  }

  &__texts {
    width: fit-content;
    color: $color-white;
    @include flexCol();
    flex-direction: column;
    position: relative;
    z-index: 1;
  }

  &__title {
    @include setFont(340px,700);
    margin-bottom: 84px;

    @include xlScreenMedia {
      font-size: 260px;
    }

    @include lgScreenMedia {
      font-size: 200px;
      margin-bottom: 40px;
    }

    @include mdScreenMedia {
      font-size: 140px;
      margin-bottom: 0;
    }

    @include smScreenMedia {
      font-size: 120px;
    }

    @media (max-width: 420px){
      font-size: 70px;
    }
  }

  &__subTitle {
    @include setFont(48px,700);
    margin-bottom: 17px;

    @include xlScreenMedia {
      font-size: 36px;
    }

    @include lgScreenMedia {
      font-size: 33px;
    }

    @include mdScreenMedia {
      display: none;
    }
  }

  &__contentText {
    @include setFont(22px,400);

    @include xxlScreenMedia {
      font-size: 18px;
    }

    @include xlScreenMedia {
      font-size: 16px
    }

    @include mdScreenMedia {
      display: none;
    }

    @include smScreenMedia {
      font-size: 14px;
    }
  }

  &__subTitleMob {
    @include setFont(33px,700);
    margin-bottom: 20px;
    color: $color-white;
    max-width: 240px;

    @media (min-width: $sm-screen) {
      display: none;
    }
  }

  &__contentTextMob {
    @include setFont(14px,400);
    margin-bottom: 30px;
    color: $color-white;

    @media (min-width: $sm-screen) {
      display: none;
    }
  }


  &__img {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    z-index: -1;

    @include mdScreenMedia {
      top: 75%;
    }
  }
}
